import Vue from 'vue'
import VueRouter from 'vue-router'

import Forum from '../views/Forum'
import AllArticle from "@/components/forum/articlelist/AllArticle";
import BoardArticle from "@/components/forum/articlelist/BoardArticle";
import Article from "@/components/forum/articlelist/article/Article";
import User from "../views/User.vue";
import AddArticle from "@/components/user/addarticle/AddArticle";
import Profile from "@/components/user/profile/Profile";
import UseDcard from "@/components/dcard/UseDcard";
import ErrorPage from "@/components/ErrorPage/ErrorPage.vue";
import Navbar from '../views/Navbar'

Vue.use(VueRouter)


const routes = [
  {
    path: '/',
    component: Navbar,
  },
  {
    path: '/dcard',
    component: UseDcard,
    children: [
      {
        name: 'UseDcard',
        path: 'usedcard',
        component: UseDcard,
      },
    ]
  },
  {
    path: '/dcard/user',
    component: User,
    children: [
      {
        name: 'AddArticle',
        path: 'addarticle',
        component: AddArticle,
      },
      {
        name: 'Profile',
        path: 'profile',
        component: Profile,
      }
    ]
  },
  {
    path: '/dcard/forum',
    component: Forum,
    children: [
      {
        name: 'AllArticle',
        path: '/dcard/forum/all',
        component: AllArticle,
      },
      {
        name: 'BoardArticle',
        path: '/dcard/forum/:boardPath',
        component: BoardArticle,

      },
      {
        name: 'Article',
        path: '/dcard/forum/:boardPath/article/:id',
        component: Article,
      },

    ]
  },
  {
    name: '404Page',
    path: '/*',
    component: ErrorPage,

  },

]

const router = new VueRouter({
  // mode: 'history',
  routes
})


// to:  from :  next()
router.beforeEach((to, from, next) => {
  const isLogin = localStorage.myToken ? true : false; //localStorage token 
  // if (to.path === '/dcard/usedcard' || to.path === '/dcard/forum/all' || to.path === '/dcard/forum/frontend' || to.path === '/dcard/forum/dressup' || to.path === '/dcard/forum/funny') {
  if (to.path === '/dcard/user/addarticle' || to.path === '/dcard/user/profile') {
    isLogin ? next() : next('/dcard/usedcard')
  } else {
    next()     //  localStorage token /login
  }
})

export default router

